import { fetchList } from '@/api/old/article';
export default {
  props: {
    type: {
      type: String,
      default: 'CN'
    }
  },
  data: function data() {
    return {
      list: null,
      listQuery: {
        page: 1,
        limit: 5,
        type: this.type,
        sort: '+id'
      },
      loading: false
    };
  },
  filters: {
    statusFilter: function statusFilter(status) {
      var statusMap = {
        published: 'success',
        draft: 'info',
        deleted: 'danger'
      };
      return statusMap[status];
    }
  },
  created: function created() {
    this.getList();
  },
  methods: {
    getList: function getList() {
      var _this = this;

      this.loading = true;
      this.$emit('create'); // for test

      fetchList(this.listQuery).then(function (res) {
        _this.list = res.data.items;
        _this.loading = false;
      });
    }
  }
};